import React from 'react';
import './App.css';
import { MPPS, Mpp } from '../data/mpps';
import { MppCard } from './MppCard';
import { GuessName } from './GuessName';
import { GuessParty } from './GuessParty';
import { GuessRiding } from './GuessRiding';
import sampleSize from 'lodash/sampleSize';
import shuffle from 'lodash/shuffle';

type AppState = {
    currentSelected: number,
    hide: boolean,
    ridingSelections: string[],
    nameSelections: string[]
}

class App extends React.Component<{}, AppState> {

    getRandomMpp = (): number => {
        return Math.floor(Math.random() * MPPS.length);
    }

    state = {
        currentSelected: 0,
        hide: true,
        ridingSelections: [],
        nameSelections: []
    }

    componentDidMount() {
        this.next();
    }

    next = () => {

        const index: number = this.getRandomMpp();

        const mpp: Mpp = MPPS[index];

        const otherMpps = [...MPPS];
        otherMpps.slice(index, 1);

        const ridingSelections: string[] = shuffle([...sampleSize(otherMpps.map(x => x.riding_name), 3), mpp.riding_name]);

        const nameSelections: string[] = shuffle([...sampleSize(otherMpps.filter(x => x.gender === mpp.gender).map(x => x.first_name + " " + x.last_name), 3), mpp.first_name + " " + mpp.last_name]);

        this.setState({
            currentSelected: index,
            hide: true,
            ridingSelections: ridingSelections,
            nameSelections: nameSelections
        })


    }

    check = () => {
        this.setState({ hide: false })
    }

    onPartyChange = (party: string) => {
        console.log(party)
    }

    onRidingChange = (riding: string) => {
        console.log(riding)
    }

    onNameChange = (name: string) => {
        console.log(name)
    }

    currentMpp = (): Mpp => {
        return MPPS[this.state.currentSelected];
    }

    render() {
        return (
            <div className="App" >
                <h1>Know Your Ontario MPP</h1>
                <h2>43rd Parliament (August 8, 2022)</h2>
                <MppCard key={this.state.currentSelected} mpp={this.state.currentSelected} hide={this.state.hide}></MppCard>
                <div className="ui centered grid container" key={'G' + this.state.currentSelected}>
                    <GuessName nameSelections={this.state.nameSelections} onChange={this.onNameChange} />
                    <GuessRiding ridingSelections={this.state.ridingSelections} onChange={this.onRidingChange} />
                    <GuessParty onChange={this.onPartyChange} />
                </div>
                <button className="ui button" onClick={this.check}>
                    Check
                </button>
                <button className="ui right labeled icon button" onClick={this.next}>
                    <i className="right arrow icon"></i>
                    Next
                </button>
            </div>
        );
    }
}

export default App;
