import React from 'react';
import './GuessParty.css';

type GuessPartyProps = {
    onChange: (party: string) => void;
}

export class GuessParty extends React.Component<GuessPartyProps> {

    onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        this.props.onChange(event.target.value)
    }

    render() {
        return (
            <div className="four wide column party" onChange={this.onChange} >
                <p>Select party:</p>
                <label>
                    <input type="radio" value="Progressive Conservative Party of Ontario" name="party" />
                    PC
                </label> <br />
                <label><input type="radio" value="New Democratic Party of Ontario" name="party" />
                    NDP</label> <br />
                <label><input type="radio" value="Ontario Liberal Party" name="party" />
                    Liberal
                </label> <br />
                <label><input type="radio" value="Green Party of Ontario" name="party" />
                    Green
                </label> <br />
                <label><input type="radio" value="Independent" name="party" />
                    Independent
                </label> <br />
            </div>
        );
    }
}

